ы отмечают, что законодательство не позволяет проводить журналистские расследования. В частности, нормы о диффамации, о неприкосновенности частной жизни тормозят развитие этого жанра. ")]),_c('p',[_vm._v("Претензии к журналистам поступают в основном от представителей государственных органов. ")]),_c('p',[_vm._v("Многие региональные редакции не могут позволить себе содержать в штате юриста. При этом отмечают, что у государственных СМИ проблем не возникает, досудебные претензии поступают к ним редко. ")]),_c('p',[_vm._v("Несмотря на установленные законодательством нормы о том, что юридические лица не могут требовать возмещения морального вреда, ПГУ (Павлодарский университет) обратился с иском на 40 млн тенге к «Павлодар онлайн», требуя опровержения новости трехлетней давности. Нередко возникают проблемы со сроками исковой давности. Поскольку закон не устанавливает такие сроки, в Шымкенте идет суд по публикации 2011 года к сетевому изданию titus.kz. Истец считает, что в материале незаконно используется его имя. Но больше всего его возмущают комментарии под материалом, которых за девять лет набралось немало.")]),_c('p',[_vm._v("Среди уголовных дел можно отметить процессы, связанные с клеветой. Корреспондент «Тобол-Инфо» Алина Сушко была оправдана за отсутствием состава преступления. Поводом послужил материал, обидевший местного капитана полиции. А вот Елену Кузнецову сначала осудили на год ограничения свободы и штраф 36 000 тенге. Позже апелляционная коллегия отменила решение суда первой инстанции, посчитав, что суд не обеспечил всестороннее, полное и объективное исследование обстоятельств дела и что частному обвинителю не удалось представить в суде доказательства «распространения не соответствующих действительности сведений».")]),_c('p',[_vm._v("Журналисты уверены, что в штате должен быть юрист, который будет просматривать все материалы до выхода в печать. Также должна быть доказательственная база по всем спорным статьям. ")]),_c('p',[_vm._v("В случае судебных претензий СМИ обычно привлекают независимую экспертизу и обращаются за помощью к адвокатам и медийным организациям. Одна из причин, по которой исков стало меньше - журналисты стали более профессиональными и грамотными.")]),_c('p',[_vm._v("Казахоязычные журналисты отмечают, что сегодня журналисту приходится балансировать в части сохранения прав на неприкосновенность частной жизни. Всеобщая гласность привела к тому, что иногда золотая середина нарушается во имя рейтингового материала. Грамотность СМИ в этой сфере надо постоянно повышать во избежание спорных моментов")]),_c('h2',[_c('span',[_vm._v("Что делать?")])]),_c('ul',[_c('li',[_vm._v("– При назначении сумм морального вреда необходимо учитывать принцип соразмерности. Истец должен доказывать нравственные страдания в суде. Штрафы должны быть разумными и соразмерными. ")]),_c('li',[_vm._v("– Установить срок исковой давности по публикациям в СМИ в один год.")]),_c('li',[_vm._v("– Обязать судей соблюдать процедуру досудебного урегулирования.")])]),_c('p',[_vm._v("Считают, что с изменением законодательства (исключение клеветы из уголовного Кодекса в КоАП РК) есть надежда на улучшение ситуации. Но не совсем уверены, что все будет так хорошо, как хотелось бы для журналистов.")]),_c('ul',[_c('li',[_vm._v("– Привести законодательство в соответствии с международными стандартами в области свободы выражения мнения.")]),_c('li',[_vm._v("– Декриминализировать наказание деяния, связанные с реализацией права на свободу выражения мнений (оскорбление, распространение заведомо ложной информации, возбуждение розни)")]),_c('li',[_vm._v("– Ввести государственную пошлину, соразмерную предъявляемым суммам исков к СМИ по возмещению морального ущерба.")]),_c('li',[_vm._v("– Ввести понятие «общественный интерес», «общественное место» и «публичное лицо» в национальное законодательство.")])])])}]

